/* Colors */
$color-blue: #B60CE8;
$color-white: #FFFFFF;
$color-black: #000000;
$color-orange: #FF710D;
$color-pink: #F50066;
$color-grey: #333333;
$color-light: #E8E8EE;

$font-family-sans-serif: "Montserrat", sans-serif;
$font-family-serif: "Sanchez", serif;

.watch {
  font-family: $font-family-sans-serif;
  text-align: center;
  font-size: 12px;
  padding: 48px 0;
}

.watch-blue {
  background-color: $color-blue;
  color: $color-white;
}

.watch-orange {
  background-color: $color-orange;
  color: $color-white;
}

.watch-pink {
  background-color: $color-pink;
  color: $color-white;
}

.watch-light {
  background-color: $color-light;
  color: $color-grey;
}

html {
  background-color: $color-white;
}

body {
  background-color: $color-white;
  font-family: $font-family-serif;;
  font-size: 18px;
  line-height: 1.8;
  color: $color-grey;
  padding: 50px 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $font-family-sans-serif;
  color: $color-blue;
  margin-top: 0;
  margin-bottom: 0;
}

p, ul { margin-top: 0; margin-bottom: 15px; }

h1 {
  font-size: 48px;
  margin-bottom: 30px;
}
h2 {
  font-size: 36px;
  color: $color-pink;
  margin-bottom: 30px;

}
h3 {
  font-size: 24px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.label {
  background-color: transparent;
  font-size: inherit;
  padding: inherit;
  font-weight: inherit;
}

a, a:hover, a::after {
  color: $color-orange;
}

.article-date {
  font-family: $font-family-sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 0;
}

.article-date-sm {
  font-size: 12px;
}

.article-modified {
  font-family: $font-family-sans-serif;
  font-style: italic;
  font-size: 12px;
  color: $color-light;
  margin-bottom: 0;
}

.article-list {
  list-style-type: square;
  padding-left: 20px;
}

.article-list li {
  padding-left: -15px;
}

.article-link {
  font-family: $font-family-sans-serif;
}

.article-text { color: $color-black; }
.article-text p { text-align: justify; }

.article-text p:first-of-type:first-letter {
  -webkit-initial-letter: 3;
  initial-letter: 3;
  margin-right: 10px;
}

hr { border: 1px solid $color-grey; }

.social-list i.fa {
display: inline-block;
border-radius: 48px;
font-size: 24px;
background-color: $color-grey;
color: $color-white;
padding: 0.5em 0.6em;
}
.cv-photo {
  width: 200px;
  border-radius: 200px;
  margin-bottom: 25px;
  margin-left:auto;
  margin-right: auto;
}

.cv-info {
  margin-bottom: 0;
}

.cv-dates {
  color: $color-pink;
  font-style: italic;
}
